header {
    width: 100%;
   padding-top: 20px;
   padding-bottom: 14px;

   @media screen and (min-width: 768px) {
    padding-top: 26px;
    padding-bottom: 34px;
    }

   @media screen and (min-width: 1200px) {
   padding-top: 24px;
   padding-bottom: 24px;
   }  


}

.header-main-div {
    display: flex;
    align-items: center;
}

.header__logo {
    display: flex;
    align-items: center;
    font-style: italic;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.11;
    letter-spacing: -0.03em;
    color: $description-color;
    text-transform: uppercase;
    text-decoration: none;
    margin-left: 4px;
    
}

.header__icon {
    width: 24px;
    height: 24px;
    fill: $text-color;
    stroke: $main-color;
}


.header-list {
    display: none;
    
        @media screen and (min-width: 1200px) {
            display: flex;
        justify-content: center;
       align-items: center;
       gap: 24px;
       margin-left: 137px;
        }  
}

.header-list__link {

    @media screen and (min-width: 1200px) {
        font-weight: 500;
        font-size: 14px;
        line-height: 1.14;
        letter-spacing: -0.02em;
        color: $description-color;
        text-decoration: none;
    }  
}

.header-list__link:hover {
    color: $main-color;
}

.header-lis-icon {
    display: flex;
    max-width: 60px;
    gap: 20px;
    margin-left: 137px;
}

.burger {
    display: none;
    width: 24px;
    height: 16px;
}

.header-lis-icon__instagram{
    width: 24px;
    height: 24px;
    fill:$text-color;
    stroke: #000000;

}

.header-lis-icon__twitter{
    width: 24px;
    height: 24px;
    fill: $text-color;
    stroke: #000000;

}

.header-lis-icon__instagram:hover{
    stroke: $main-color;

}

.header-lis-icon__twitter:hover {
    stroke: $main-color;
}